.footer-container {
  background-color: #242424;
  padding: 4rem 0rem 2rem 4rem;
  display: grid;
  grid-template-columns: repeat(2, auto);
  font-size: 15px;
  flex-direction: inline;
  justify-content: center;
  align-items: top;  
  width: 100%;
 

}






