.footer-links {
  /* width: 100%; */
  /* max-width: 1000px; */
  display: flex;
  /* justify-content: center; */
}

.footer-link-wrapper {
  
  display: flex;
}

.footer-link-items {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: justify;
  justify-content: start;
  box-sizing: border-box;
  width: 500px;
 
  
  ;
}

.footer-link-items h2 {
  margin-bottom: 16px;
}

.footer-link-items > h2 {
  color: #fff;
}

.footer-link-items a {
  color: #fff;
  text-decoration: none;
  margin-bottom: 0.5rem;
}

.footer-link-items p {
  color: rgb(143, 139, 139);
  line-height: 1.1rem;
  text-decoration: none;
  
}
.footer-link-items a:hover {
  color: #e9e9e9;
  transition: 0.3s ease-out;
}

.footer-email-form h2 {
  margin-bottom: 2rem;
}

.footer-input::placeholder {
  color: #b1b1b1;
}

/* Social Icons */
.social-icon-link-footer {
  color: #fff;
  padding-top: 20px;;
  font-size: 25px;
}

.social-media {
  max-width: 1000px;
  width: 100%;
}

.social-media-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1000px;
  margin: 40px auto 0 auto;
}

.social-icons-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 50%;
}

.social-logo {
  color: #fff;
  justify-self: start;
  /* margin-left: 20px; */
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.website-rights {
  color: #fff;
  margin-bottom: 16px;
}

@media screen and (max-width: 960px) {


  .footer-container{
    display: flex;
    flex-direction: column;
    color : red;
  }

  .footer-links {
    padding-top: 2rem;
  }

  .footer-input {
    width: 100%;
  }

  .btn {
    width: 100%;
  }

  .footer-link-wrapper {
    flex-direction: column;
    
    
  }

  .social-media-wrap {
    flex-direction: column;
  }

  .footer-link-items{
    padding-bottom: 1.5rem;
  }
  
  .footer-link-items > h2 {
   font-size: 1rem;
   line-height: 0.5rem;
  }
}

